import Vue from 'vue'
import App from './App'
import router from './router'
import MintUI from 'mint-ui';
import 'mint-ui/lib/style.css';
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

// import VueResource from 'vue-resource'

// //VueResourcecls
// Vue.use(VueResource)
// Vue.use(Mint);
Vue.use(MintUI);

Vue.config.productionTip = false

const store = new Vuex.Store({
  state:{
    isshow:false,
    islogin:false,
    user:false,
    token:false
  },
  mutations:{
    login(state,user){
      state.user=user;
    },
    logout(state,user){
      state.user=false;
    }
  },
  actions:{}
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

