<template>
<div>
<mt-header fixed title="EasyLib">
</mt-header>
<div class="page-swipe">
<div class="main">
    
</div>
<mt-swipe :auto="4000" >
    <mt-swipe-item class="slide1" v-for="item in items" :key="item.id">
        <router-link to="/">
        <img :src="item.url" class="img" />
        </router-link>
    </mt-swipe-item>
</mt-swipe>
</div>
<div>
    <router-link tag="section"  to=""  v-for="item in NEWS" :key="item.postid">
         <ul >
             <!-- v-infinite-scroll="loadMore" -->
             <li><a href="">{{item.title}}</a>{{item.pos}}</li>
         </ul>
    </router-link>
  <!-- <div id="content-list">
    <router-link tag="section" class="m_article clearfix" :to="{ path: 'article', query: { id: item.postid}}"
                    v-for="item in NEWS" :key="item.postid">
        <a :href="{ path: 'article', query: { id: item.postid }}">
            <div class="m_article_img">
                <img :src="item.imgsrc">
            </div>
            <div class="m_article_info">
                <div class="m_article_title">
                    <span>{{item.title}}</span>
                </div>
                <div class="m_article_desc ">
                    <div class="m_article_desc_l">
                        <span class="m_article_channel">{{item.source}}</span>
                        <span class="m_article_time">  {{item.ptime}}</span>
                    </div>

                </div>
            </div>
        </a>
    </router-link>
</div> -->
</div>
</div>

</template>
<script>
import axios from 'axios'
    export default {
        data(){
            return{
                items:[{
                    url: require('../../../assets/img/1.jpg')
                }, {
                    url: require('../../../assets/img/2.jpg')
                },{
                    url: require('../../../assets/img/3.jpg')
                }
                ],
                NEWS:[]
            }
        },
        created(){
        axios.get('http://localhost:8080/static/oftenGoods.php')
        .then(response=>{
            console.log(response);
            console.log(response.data[0]);
            
            this.NEWS=response.data;
        })
        .catch(error=>{
            console.log(error);
            alert('网络错误，不能访问');
        })
        }
    }
</script>
<style>
.mint-swipe-indicator{
background-color:darkorange;
}
.mint-swipe-indicator .is-active{
    background-color: aqua;
}
.mint-swipe {
    width: 100%;
    height: 200px;
    color: #fff;
    font-size: 30px;
    text-align: center;
}
.mint-swipe{
    display: block;
    height: 200px;
}
img {
 width: 100%;
 height: 100%;
 }
 .main{   
     margin-top:40px;
 }

 ul, li { 
margin-left: -5px;
list-style:none; 
} 
 li { 
font-size: 14px;
width:100%; 
height: 28px; 
text-align: center; 
padding-left: 0px; 
line-height:28px; 
color:#CCC; 
border-bottom:dashed 1px #CCC; 
} 
 li a { 
float:left; 
text-align:left; 
line-height:28px; 
color:#666; 
text-decoration:none; 
} 
 li a:hover { 
color:#F00; 
} 
</style>


