import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/pages/index/index'
import my from '@/components/pages/my/my'
import zuoxiang from '@/components/pages/zuoxiang/zuoxiang'
import shushi from '@/components/pages/shushi/shushi'
import login from '@/components/pages/my/login'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      alias: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/my',  
      component: my,
      meta:{
        requireAuth:true
      },
      children:[
        {path:'/',component:my},
        { path: '/login', component: login}
      ]   
    },{
      path: '/zuoxiang',
      name: 'zuoxiang',
      component: zuoxiang,
      meta:{
        requireAuth:true
      }
    },{
      path:'/shushi',
      name:'shushi',
      component:shushi
    },{
      path:'/login',
      name:'login',
      component:login
    }

  ]
})



