<template>
  <div id="app">

     <router-view/>
    
      <mt-tabbar v-model="selected" fixed >  
    
        <mt-tab-item v-for="item in index" :key="item.index" :id="item.id" >  
        <i slot="icon" :class="item.icon"></i>
        {{item.name}}  
        
      </mt-tab-item>
      <!-- <mt-tab-item id="zuoxiang" >  
        <i slot="icon" class="icon iconfont icon-zhishi"></i>
        座享  
      </mt-tab-item>  
      <mt-tab-item id="shushi" >  
        <i slot="icon" class="icon iconfont icon-shu"></i> 
        书识 
      </mt-tab-item>  
      <mt-tab-item id="my" >  
        <i slot="icon" class="icon iconfont icon-wo"></i> 
        我的  
      </mt-tab-item>   -->
    </mt-tabbar>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'App',
  data() {  
    return {  
      selected: 'index',
      index:[{
        id:"index",
        name:"首页",
        icon:"icon iconfont icon-shouye"
      },{
        id:"zuoxiang",
        name:"书享",
        icon:"icon iconfont icon-zhishi"
      },{
        id:"shushi",
        name:"书识",
        icon:"icon iconfont icon-shu"
      },{
        id:"my",
        name:"我的",
        icon:"icon iconfont icon-wo"
      }]
    }  
  },
  watch: {
    selected: function (val, oldVal) {
        // 这里就可以通过 val 的值变更来确定
        console.log(val);
        this.$router.push(val);
    },
  
    
}}
</script>

<style>

/* #app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
} */
.page-tabbar {  
  overflow: hidden;  
  height: 100vh;  
}

body{
  margin: 0px;
}

</style>



